.Movie{background-color:#fff;width:40%;display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-align:start;align-items:flex-start;-ms-flex-wrap:wrap;flex-wrap:wrap;margin-bottom:50px;padding:0 20px;-webkit-box-shadow:0 8px 38px hsla(0,0%,52%,.3),0 5px 12px hsla(0,0%,52%,.22);box-shadow:0 8px 38px hsla(0,0%,52%,.3),0 5px 12px hsla(0,0%,52%,.22)}
.Movie,.Movie__Column{-o-text-overflow:ellipsis;text-overflow:ellipsis}
.Movie__Column{width:30%;-webkit-box-sizing:border-box;box-sizing:border-box}
.Movie__Column:last-child{padding:20px 0;width:60%}
.Movie h1{font-size:20px;font-weight:600}
.Movie .Movie__Genres{display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;margin-bottom:20px}
.Movie__Genres .Movie__Genre{margin-right:10px;color:#b4b5bd}
.Movie .Movie__Synopsis{-o-text-overflow:ellipsis;text-overflow:ellipsis;color:#b4b5bd;overflow:hidden}
.Movie .Movie__Poster{max-width:100%;position:relative;top:-20px;-webkit-box-shadow:-10px 19px 38px rgba(83,83,83,.3),10px 15px 12px rgba(80,80,80,.22);box-shadow:-10px 19px 38px rgba(83,83,83,.3),10px 15px 12px rgba(80,80,80,.22)}

@media screen and (min-width:320px) and (max-width:667px){
    .Movie{width:100%}
}

@media screen and (min-width:320px) and (max-width:667px) and (orientation:portrait){
    .Movie{width:100%;-ms-flex-direction:column;flex-direction:column}
    .Movie__Poster{top:0;left:0;width:100%}
    .Movie__Column{width:100%!important}
}


